@tailwind base;
@tailwind components;
@tailwind utilities;

.btn {
    @apply 
    bg-white 
    text-black 
    font-bold 
    py-3  
    rounded-full 
    shadow
    text-[1rem] 
    w-[10.2rem]
    hover:bg-[#5ADBFF]
    hover:text-white
    transition duration-200 ease-out;
}

.btn-primary {
    @apply bg-blue-500 text-white hover:bg-blue-600;
}

.main-content strong {
    @apply font-black
}